$(function () {
	gsap.registerPlugin(ScrollTrigger);

	let targetBlock = $('.js-steps');
	
	if (targetBlock.length) {
		let cards = $('.steps__item');

		function positionCards() {
			let top = 0;
			let distance = 0;
			let step = 1;
			let scrollHeight = window.innerHeight / 2;
			let targetBlockOffset = targetBlock.offset().top;
			// let targetBlockHeight = targetBlock.outerHeight();

			$.each(cards, function () {
				$(this).css('top', top);
				$(this).css({
					'transform': 'translateY(' + distance + 'vh)'
				});

				gsap.to($(this), {
					scrollTrigger: {
						trigger: '.js-steps',
						start: (targetBlockOffset - window.innerHeight) + scrollHeight * step,
						end: (targetBlockOffset - window.innerHeight) + scrollHeight * step + scrollHeight,
						scrub: true,
						// markers: true,
					},
					y: 0,
					// ease: 'power1.inOut',
					// duration: 2
				});

				// top += 70;
				$(window).width() < 768 ? top += 64 : top += 70
				distance = 100;
				step += 1;
			});
		}

		positionCards();

		$(window).scroll(function () {
			let currentScroll = $(window).scrollTop();
			let targetBlockOffset = Math.floor(targetBlock.offset().top);
			let targetBlockHeight = Math.floor(targetBlock.outerHeight());
			let range = $('.js-steps-range');
			let scrollProgress = ((currentScroll - targetBlockOffset) / (targetBlockHeight - window.innerHeight)) * 100;

			if (scrollProgress <= 0) {
				range.css('left', 1 + '%');
			} else if (scrollProgress >= 100) {
				range.css('left', 100 + '%');
			} else {
				range.css('left', scrollProgress + '%');
			}
		});
	}
})